<template>
    <main class="app-main">
        <section class="top">
            <p class="title">
                <md-icon class="go-back" name="arrow-left" size="lg" @click="goBack"></md-icon>我的项目</p>
        </section>
        <section class="middle">
            <div class="md-example-child md-example-child-tabs md-example-child-tab-bar-6 top-tab-bar">
                <md-tab-bar
                        v-model="current"
                        :items="items"
                        :has-ink="false"
                        @change="onTabChange"
                ></md-tab-bar>
                <div class="none" v-if="!listLeft.length"  :style="{height: Height}">暂无数据</div>
                <md-scroll-view v-if="listLeft.length" :style="{height: Height}" @end-reached="$_onEndReached" :auto-reflow="true" ref="scrollView" :scrolling-x="false" @scroll="$_onScroll">
                    <div
                            v-for="i in listLeft"
                            class="scroll-view-item"
                            :key="i.id"
                            @click="$_onItemClick(i)">
                        <img :src="i.coverPicture" alt="">
                        <div class="article">
                            <p v-html="i.title"></p>
                            <span><img src="@/assets/home/icon_time@2x.png" alt="">{{i.createTime}}</span>
                        </div>
                    </div>
                    <md-scroll-view-more slot="more" :is-finished="isFinished">
                    </md-scroll-view-more>
                </md-scroll-view>
            </div>
        </section>
    </main>
</template>

<script>
import { TabBar, Swiper, SwiperItem, ScrollView, Button, Icon, CellItem, Toast, ScrollViewMore } from 'mand-mobile'
import MdSwiperItem from 'mand-mobile/components/swiper/swiper-item'
import { getList } from '@/apiUrl/personal/project.js'
export default {
  name: 'News',
  components: {
    MdSwiperItem,
    [TabBar.name]: TabBar,
    [Swiper.name]: Swiper,
    [SwiperItem.name]: SwiperItem,
    [CellItem.name]: CellItem,
    [Button.name]: Button,
    [ScrollView.name]: ScrollView,
    [Icon.name]: Icon,
    [ScrollViewMore.name]: ScrollViewMore
  },
  data () {
    return {
      // 当前选中tab
      current: 1,
      // tab项
      items: [{ name: 1, label: '申报中' }, { name: 2, label: '申报成功' }, { name: 3, label: '申报失败' }],
      listLeft: [],
      isFinished: false,
      Height: '900px',
      searchParams: {
        status: 1,
        pageSize: 10,
        pageNumber: 1
      },
      listRight: 10,
      name: '减肥法绝对是范德萨范德萨范德萨范德萨范德萨'
    }
  },
  mounted () {
    console.log(window.innerHeight)
    this.getList()
    this.Height = window.innerHeight - 84 + 'px'
  },
  methods: {
    deArgee () {

    },
    getList () {
      getList(this.searchParams).then(res => {
        this.listLeft = res.data
      }).catch(err => {
        Toast.failed(err)
      })
    },
    argee () {
      this.$router.push('/personal/date')
    },
    onSwiperChange (from, to) {
      this.current = to
    },
    onTabChange (item, index) {
      this.searchParams.pageNumber = 1
      this.searchParams.status = item.name
      this.getList()
    },
    // 跳转新闻详情页
    $_onItemClick (i) {
      this.$router.push({
        path: '/project/detail',
        query: { id: i.id }
      })
    },
    $_onEndReached () {
      if (this.isFinished) {
        return
      }
      this.searchParams.pageNumber++
      getList(this.searchParams).then(res => {
        this.listLeft = this.listLeft.concat(res.data)
        if (res.data.length === 0) {
          this.isFinished = true
        }
        this.$refs.scrollView.finishLoadMore()
      }).catch(err => {
        Toast.failed(err)
      })
    },
    $_onScroll ({ scrollLeft, scrollTop }) {
      // console.log(`[Mand Mobile ScrollView - demo0 - onScroll] scrollLeft: ${scrollLeft}, scrollTop: ${scrollTop}`)
    },
    // 回退
    goBack () {
      this.$router.go(-1)
    }
  }
}
</script>

