import request from '../../utils/request'
import apiPath from '../url'
import qs from 'qs'
// 获取收藏页列表
export function getList (data) {
    data = qs.stringify(data)
    console.log(data)
    return request({
        url: apiPath.personal.project.getList,
        method: 'post',
        data: data
    })
}
